import * as A from "../../assets";
import * as S from "./style";
import * as T from "../Typography";

export default function ListHeader({
  text,
  nmbOfTasks,
  topBorderColor,
}) {
  return (
    <S.Container topBorderColor={topBorderColor}>
      <T.Flex>
        <T.P size="13px">{text}</T.P>
        <S.NmbOfTasksInList>{nmbOfTasks}</S.NmbOfTasksInList>
      </T.Flex>
      <T.Flex margin="2px 0 0">
        <S.ArrowContainer>
          <T.ToolTip text="collapse column">
            <T.SvgContainer
              rotate="180deg"
              fill="true"
              color="rgba(52,52,52,.6)"
              width="5px"
              margin="-3px 12px 0"
            >
              {A.arrowEmptyMiddle}
            </T.SvgContainer>
          </T.ToolTip>
        </S.ArrowContainer>
        <T.SvgContainer fill="true" color="#979797" width="15px">
          {A.menuCollapse}
        </T.SvgContainer>
      </T.Flex>
    </S.Container>
  );
}
