let id = 4;

export function reducer(state, action) {
  switch (action.type) {
    case "ADD_TASK":
      return {
        ...state,
        tasks: [...state.tasks, { id: id++, ...action.payload }],
      };
    case "EDIT_TASKS":
      return { ...state, tasks: action.payload };
    case "ADD_USER":
      return { ...state, user: action.payload };
    case "CLEAR_USER":
      return { ...state, user: {} };
    default:
      console.log("Nothing match the type of action");
  }
}
