import * as S from "./style";
import * as T from "../../components/Typography";

import { useEffect, useState } from "react";

import Card from "../../components/SignCard";
import Footer from "../../components/SignFooter";
import Header from "../../components/SignHeader";
import WelcomePopUp from "../../components/LoadingPopUp";
import { useStateValue } from "../../components/StateProvider";

export default function Sign({ type }) {
  const [{ user }] = useStateValue();
  const [displayWelcome, setDisplayWelcome] = useState(false);
  useEffect(() => {
    if (user.name) {
      setDisplayWelcome(true);
      setTimeout(() => {
        setDisplayWelcome(false);
      }, 2000);
    }
    // return () => {
    //   cleanup
  }, [user.name]);
  console.log(user);
  return (
    <>
      <S.Container>
        <T.Content padding="0 30px">
          <WelcomePopUp isDisplayed={displayWelcome}></WelcomePopUp>
          <Header type={type} />
          <Card setDisplayWelcome={setDisplayWelcome} type={type} />
          <Footer type={type} />
        </T.Content>
      </S.Container>
    </>
  );
}
