import * as S from "./style";

import Header from "../ListHeader";
import Task from "../ListTask";
import { useState } from "react";
import { useStateValue } from "../StateProvider";

const getNmbOfCompletedTasks = (tasks) => {
  let count = 0;
  tasks.map((value) => value.isCompleted && count++);
  return count;
};
const getNmbOfUnCompletedTasks = (tasks) => {
  let count = 0;
  tasks.map((value) => !value.isCompleted && count++);
  return count;
};

export default function HomeMain() {
  const [{ tasks }] = useStateValue();
  const [nmbOfCompletedTasks, setNmbOfCompleted] = useState(
    getNmbOfCompletedTasks(tasks)
  );
  const [nmbOfUnCompletedTasks, setNmbOfUnCompleted] = useState(
    getNmbOfUnCompletedTasks(tasks)
  );
  return (
    <S.Container>
      <S.ListsContainer>
        <S.List>
          <Header text="TO DO" nmbOfTasks={nmbOfUnCompletedTasks} />
          {tasks.map((value) => {
            return (
              !value.isCompleted && (
                <Task
                  key={value.id}
                  header={value.title}
                  body={value.desc}
                />
              )
            );
          })}
        </S.List>
        <S.List>
          <Header
            text="Complete"
            nmbOfTasks={nmbOfCompletedTasks}
            topBorderColor="rgb(107, 201, 80)"
          />
          {tasks.map(
            (value) =>
              value.isCompleted && (
                <Task
                  key={value.id}
                  header={value.title}
                  body={value.desc}
                />
              )
          )}
        </S.List>
      </S.ListsContainer>
    </S.Container>
  );
}
